import { NextRequest, NextResponse } from 'next/server';

export async function POST(request: NextRequest) {
  try {
    const formData = await request.formData();
    const file = formData.get('resume') as File;
    const userId = formData.get('userId') as string;

    if (!file || !userId) {
      return NextResponse.json({ error: 'Missing file or userId' }, { status: 400 });
    }

    if (file.type !== 'application/pdf') {
      return NextResponse.json({ error: 'Only PDF files are allowed' }, { status: 400 });
    }

    // Convert file to buffer
    const buffer = Buffer.from(await file.arrayBuffer());
    
    // For now, use placeholder text - user will need to manually enter resume details
    const resumeText = `PDF file uploaded: ${file.name}. Please manually enter your resume details in the text area below.`;

    return NextResponse.json({ 
      success: true, 
      resumeText
    });

  } catch (error) {
    console.error('Error uploading resume:', error);
    return NextResponse.json({ 
      error: 'Failed to upload and process resume' 
    }, { status: 500 });
  }
}